export default {
	data() {
		return {
			editTitle: '',
			editType: ''
		}
	},
	onLoad(opt) {
		this.editType = opt.type
		if(opt.type == 'name') {
			this.editTitle = '编辑昵称'
		}else if(opt.type == 'idcard') {
			this.editTitle = '编辑身份证'
		}
	}
}
